import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { DatabaseProvider } from '../../providers/database/database';

import { MenuController } from 'ionic-angular';
/**
 * Generated class for the LogInPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-log-in',
  templateUrl: 'log-in.html',
})
export class LogInPage {
	username: string;
	password: string;
  constructor(public navCtrl: NavController, public navParams: NavParams, private database: DatabaseProvider, public menu: MenuController) {
    this.menu.swipeEnable(false);
  }

  createUser(){
  	this.database.CreateUser(this.username,this.password).then((data) => {
  		console.log(data);
  	}, (error) => {
  		console.log(error);
  	})
  }

  getUsers(){
  	this.database.GetAllUsers().then((data) => {
        console.log(data);
      }, (error) => {
        console.log(error);
      })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LogInPage');
  }

}



