import { Http } from '@angular/http';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

import { SQLite, SQLiteObject } from '@ionic-native/sqlite';

/*
  Generated class for the DatabaseProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class DatabaseProvider {

	private db: SQLiteObject;
	private isOpen: boolean;
  constructor(
  	public http: Http,
  	public storage: SQLite
  	) {
  	if(!this.isOpen){
  		this.storage = new SQLite();
  		this.storage.create({name: "base.db", location:"default"}).then((db:SQLiteObject) => {
  			this.db = db;
  			db.executeSql("CREATE TABLE IF NOT EXISTS usuario (id INTEGER PRIMARY KEY AUTOINCREMENT, nombre text, contrasenia text)",[]);
  			this.isOpen = true;
  		}).catch((error) => {
  			console.log(error);
  		})
  	} 
  }

  CreateUser(nombre: string, contrasenia: string){
  	console.log(nombre);
  	console.log(contrasenia);
  	return new Promise((resolve,reject) => {
  		let sql = "INSERT INTO usuario(nombre,contrasenia) VALUES(?,?)";
  		this.db.executeSql(sql,[nombre,contrasenia]).then((data) => {
  			resolve(data);
  		}, (error) => {
  			reject(error);
  		});
  	});
  }

  GetAllUsers(){
  	return new Promise((resolve,reject) => {
  		this.db.executeSql("SELECT * FROM usuario",[]).then((data) => {
  			let arrayUsers = [];
  			if(data.rows.length > 0){
  				for(var i = 0; i < data.rows.length; i++){
  					arrayUsers.push({
  						nombre: data.rows.item(i).nombre,
  						contrasenia: data.rows.item(i).contrasenia
  					});
  				}
  			}
  			resolve(arrayUsers);
  		}, (error) => {
  			reject(error);
  		})
  	})
  }
}



